import React,{Component} from 'react';
import Data from '../data/data.json';

class Summery extends Component{
  render(){
    return(
      <section>
      <h5> SUMMARY </h5>
      <ul>
      {Data.Basics.desc.map((item)=>{
        return <li> {item} </li>;
      })}
      </ul>
      </section>
    )
  }
}

export default Summery;
