import React,{Component} from 'react';
import Data from '../data/data.json';

class Skill extends Component{
  render(){
    return(
      <section>
<h5> SKILL SET </h5>

<h6> Languages </h6>
        {Data.Basics.skill.languages.map((pwa)=>{
          return <span> {pwa} </span>;
        })}

<h6> Frameworks </h6>

        {Data.Basics.skill.frameworks.map((frame)=>{
          return <span> {frame} </span>;
        })}

<h6> Libraries </h6>
        {Data.Basics.skill.libraries.map((frame)=>{
          return <span> {frame} </span>;
        })}

        <h6> Data bases </h6>
                {Data.Basics.skill.database.map((db)=>{
                  return <span> {db} </span>;
                })}

                <h6> CMS </h6>
                        {Data.Basics.skill.cms.map((cm)=>{
                          return <span> {cm} </span>;
                        })}
      </section>
    )
  }
}

export default Skill;
